.card {
  background: #fff;
  border-radius: 2px;
  height: 180px;
  margin: 10px;
  position: relative;
  width: 280px;
  box-shadow: 0 3px 6px rgb(49, 40, 71), 0 3px 6px rgb(158, 144, 196);
  text-align: left;
  margin-top: 50px;
}

.card > .img-container {
  height: 100%;
  overflow: hidden;
  text-align: center;
  background:rgb(188, 163, 224);
}
.card > .img-container > img {
  width: 60%;
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

div.img-container img {
  padding-top: 8px;
}







.wrapper {
  padding-top: 50px;
  background: url("https://d2v9y0dukr6mq2.cloudfront.net/video/thumbnail/SHUvqPx7giw9eenf2/videoblocks-bw-3d-low-poly-crystal-cave-tunnel-loopable-motion-background_bnc5w5b3f_thumbnail-full06.png");
  
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: row wrap;
          flex-flow: row wrap;
  padding: 20px;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-content: flex-start;
          align-content: flex-start;
  overflow: auto;
}

html, body, #root {
  padding: 0;
  margin: 0;
  margin-left: 100px;
  margin-right: 100px;
  height: 100%;
}

.title {
  color: rgb(224, 223, 223);
}

.scores {
  color: rgb(224, 223, 223);
}

.header {
  width: 100%;
  font-size: 2rem;
  color: rgb(0, 0, 0);
  margin-bottom: 10px;
}

.title {
  font-family: 'Candal', sans-serif;
  font-size: 40px;
  float: left;
}

.scores {
  font-family: 'VT323', monospace;
  float: right;
}


